


























































import Vue from "vue";

interface IPropItem {
  required: boolean;
  prop: string;
  localProp: string;
  type: Array<string>;
  default: string;
  watch: string;
}

function toUpperCase(string: string) {
  if (string.length === 0) {
    return string;
  }
  return string[0].toUpperCase() + string.slice(1);
}

function toLowerCase(string: string) {
  if (string.length === 0) {
    return string;
  }
  return string[0].toLowerCase() + string.slice(1);
}

const headers = [
  {
    text: "required",
    value: "required"
  },
  {
    text: "prop",
    value: "prop"
  },
  {
    text: "localProp",
    value: "localProp"
  },
  {
    text: "type",
    value: "type"
  },
  {
    text: "default",
    value: "default"
  },

  {
    text: "watch",
    value: "watch"
  }
];

const typeDefaults: any = {
  String: `""`,
  Number: "0",
  Boolean: "false",
  Object: `() => {
    return {};
  }`,
  Array: `() => {
    return [];
  }`,
  Function: `() => {
    return function() {};
  }`,
  Promise: `() => {}`,
  Date: `() => {}`,
  Symbol: `() => {
    return Symbol();
  }`
};

const typeTS: any = {
  String: "string",
  Number: "number",
  Boolean: "boolean",
  Array: "Array",
  Function: "Function",
  Promise: "Promise",
  Date: "Date",
  Symbol: "Symbol"
};

const defaultType = ["String"];

export default Vue.extend({
  data() {
    return {
      snack: false,
      snackColor: "",
      snackText: "",
      max25chars: (v: any) => v.length <= 45 || "Input too long!",
      pagination: {},
      headers,
      desserts: Array.from(Array(20), () => {
        const a: any = {};
        headers.forEach(({ value }) => {
          a[value] = "";
        });
        return a;
      }),
      typeList: Object.keys(typeDefaults)
    };
  },
  methods: {
    save() {
      this.snack = true;
      this.snackColor = "success";
      this.snackText = "Data saved";
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
      this.snack = true;
      this.snackColor = "info";
      this.snackText = "Dialog opened";
    },
    close(field: string, item: IPropItem) {
      switch (field) {
        case "prop":
          item.localProp = `local${toUpperCase(item[field])}`;
          item.watch = `set${toUpperCase(item[field])}`;
          item.required = false;
          this.setItemType(defaultType, item);
          break;
        case "type":
          this.setItemType(item[field], item);
          break;
        default:
          break;
      }
    },
    setItemType(type: Array<string>, item: IPropItem) {
      if (type) {
        item.default = typeDefaults[type[0]];
      } else {
        item.default = ""; //typeDefaults[item[field]];
      }
    },
    exportTSCode() {
      const desserts = this.desserts.filter((item: IPropItem) => {
        return item.prop.trim().length;
      });
      const code =
        desserts
          .map((item: IPropItem) => {
            const type = item.type || ["String"];
            return `
        @Prop({
          type:${type.length > 1 ? "[" + type + "]" : type},
          default:${item.default},
          required:${item.required}
        })
        ${item.prop}!:${typeTS[type[0]]};
        @Watch("${item.prop}")
        ${item.watch}(value:${typeTS[type[0]]}){
          this.${item.localProp}=value
        }`;
          })
          .join("\n") +
        "\n\n" +
        desserts
          .map((item: IPropItem) => {
            const type = item.type || ["String"];
            return `${item.localProp}:${typeTS[type[0]]};`;
          })
          .join("\n") +
        "\n\n" +
        "mounted() {\n" +
        desserts
          .map((item: IPropItem) => {
            return `this.${item.watch}(this.${item.prop});`;
          })
          .join("\n") +
        "\n}";
      console.log(code);
    }
  }
});
